import { BpmnModelElement } from "./BpmnModelElement";
import { IClrTypeElement } from "./IClrTypeElement";
import { RadixConvert } from "./RadixConvert";

export class BpmnModelHelper {
    private static elementTypeDic: { [key: string]: IClrTypeElement } = {};
    private static generatedId: number = parseInt(Date.now().toString());

    static GenerateElementId(): string {
        ++(this.generatedId);

        return "_" + RadixConvert.Decimal2String(this.generatedId);
    }

    static SetAttributeValue(obj: any, name: string, value: any): void {
        if (obj == undefined || obj == null || value == undefined || value == null)
            return;

        obj[name] = value;
    }

    static RegisterElementType(typeInstance: IClrTypeElement): void {
        BpmnModelHelper.elementTypeDic[typeInstance.ClrTypeID] = typeInstance;
    }

    static GetElementType(typeId: string): BpmnModelElement {
        return BpmnModelHelper.elementTypeDic[typeId].NewInstance();
    }
}